import './style/TotalPrice.scss';
import { useAppSelector } from '../../../../store';
import { sumBasket, sumSaleBasket } from '../basketFunction';

export const TotalPrice: React.FC = () => {
  const { sale, products } = useAppSelector((state) => state.basket);

  return (
    <div className='totalprice'>
      {sale != 0 ? (
        <>
          <div className='totalprice__price totalprice__block'>
            <div className='totalprice__text'>
              <p className='mini'>Price without discount</p>
            </div>
            <div className='totalprice__number'>
              <p className='basket'>{`${sumBasket(products)}$`}</p>
            </div>
          </div>
          <div className='totalprice__sale totalprice__block'>
            <div className='totalprice__text'>
              <p className='mini'>Discount</p>
            </div>
            <div className='totalprice__number'>
              <p className='mini'>10%</p>
            </div>
          </div>
          <div className='totalprice__total totalprice__block'>
            <div className='totalprice__text'>
              <p className='mini'>Total </p>
            </div>
            <div className='totalprice__number'>
              <p className=' sale'>{`${sumSaleBasket(products, sale)}$`}</p>
            </div>
          </div>
        </>
      ) : (
        <div className='totalprice__total notprice totalprice__block'>
          <div className='totalprice__text'>
            <p className='mini'>Price without discount </p>
          </div>
          <div className='totalprice__number'>
            <p>{`${sumBasket(products)}$`}</p>
          </div>
        </div>
      )}
    </div>
  );
};
