import { useAppSelector } from '../../../../store';
import { ProductBasketT } from '../../../../store/goodsSlices/goods.config';
import { ProductBasket } from '../ProductBasket/ProductBasket';

export const BasketBodyOne: React.FC = () => {
  const { products } = useAppSelector((state) => state.basket);

  return (
    <div className='productbasket__goods'>
      {products.map((item: ProductBasketT) => {
        return <ProductBasket product={item} key={item.id} />;
      })}
    </div>
  );
};
