import React, { useEffect } from 'react';
import InputErrFoot from './components/InputErrFoot';
import { change, focus, renderInput } from './components/inputFocus';
import { valid } from './components/validate';
import { InputFiledsTypes } from '../../../interface/ui.interface';
import { InputFiledTypes } from './Input.config';
import { useAppSelector } from '../../../store';

import './style/Input.scss';

export default function OtherInpurt({
  type,
  register,
  name,
  errors,
  setValue,
  textFiled,
}: InputFiledsTypes<any>) {
  const err = errors[name] && (errors[name]?.message || 'Error');
  const { sale } = useAppSelector((state) => state.basket);

  const changeSale = (e) => {
    if (sale > 0) setValue(name, 'aaa');
    else change(e, setValue, name);
  };

  useEffect(() => {
    renderInput();
  }, []);

  return (
    <div className='inputFiled'>
      <div
        className={
          sale > 0 ? 'inputFiled__block sale ' : 'inputFiled__block old all'
        }
      >
        <div className='inputFiled__text old all'>
          <p className='mini'>{textFiled}</p>
        </div>
        <input
          {...register(name, {
            required: 'Field must be filled',
            pattern: valid(type, name),
          })}
          onChange={(e) => changeSale(e)}
          onFocus={focus}
          onBlur={focus}
          type={type}
        />
      </div>
      {err && <InputErrFoot err={err} />}
    </div>
  );
}

export function AllInpurt({
  type,
  register,
  name,
  setValue,
  textFiled,
}: InputFiledTypes<any>) {
  useEffect(() => {
    renderInput();
  }, []);

  return (
    <div className='inputFiled'>
      <div className={'inputFiled__block old all'}>
        <div className='inputFiled__text old all'>
          <p className='mini'>{textFiled}</p>
        </div>
        <input
          {...register(name, {
            required: 'Field must be filled',
            pattern: valid(type, name),
          })}
          onChange={(e) => change(e, setValue, name)}
          onFocus={focus}
          onBlur={focus}
          type={type}
        />
      </div>
    </div>
  );
}
