import { useEffect } from 'react';
import { FieldValues, SubmitHandler, useForm } from 'react-hook-form';
import './style/SaleBlock.scss';
import { Button } from '../../../../components';
import OtherInpurt from '../../../../components/UI/Input/OtherInput';
import { useAppDispatch, useAppSelector } from '../../../../store';
import { setSale } from '../../../../store/goodsSlices/basketSlice';

const SaleBlock: React.FC = () => {
  const {
    register,
    formState: { errors },
    handleSubmit,
    setValue,
  } = useForm<FieldValues>({ mode: 'onChange' });
  const dispatch = useAppDispatch();
  const { sale } = useAppSelector((state) => state.basket);
  const submit: SubmitHandler<FieldValues> = () => {
    if (sale > 0) {
      dispatch(setSale({ sale: 0 }));
      setValue('code', '');
    } else {
      dispatch(setSale({ sale: 10 }));
    }
  };

  useEffect(() => {
    if (sale > 0) {
      setValue('code', 'abcAD55');
    }
  }, []);

  return (
    <form onSubmit={handleSubmit(submit)} className='setsale'>
      <div className='setsale__input'>
        <OtherInpurt
          register={register}
          type='text'
          setValue={setValue}
          errors={errors}
          name='code'
          textFiled='Promocode'
        />
      </div>
      <div className='setsale__btn'>
        <Button name='sale'>
          <p className='mini'>{sale != 0 ? 'delete' : 'apply'}</p>
        </Button>
      </div>
    </form>
  );
};

export default SaleBlock;
